.link {
    color: white;
    width: 450px;
    height: 48px;
    text-align: center;
    background-color: black;
    margin-bottom: 10px;
    border-radius: 50px;
    text-transform: uppercase;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 48px;
}

.btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h5 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 50px 0 10px 0;
}

img {
    width: 20px;  
    height: auto;
    margin-right: 9px;
}

.link:first-child {
    background-color: #1877F2;
}

.link:nth-child(2) {
    background-color: rgba(0, 0, 0, 0.9);
}

.link:nth-child(3) {
    color: rgba(63, 63, 63, 0.9);
    background-color: white;
    border: 1px solid rgba(99, 99, 99, 0.9);
}

.link:nth-child(4) {
    color: rgba(63, 63, 63, 0.9);
    background-color: white;
    border: 1px solid rgba(159, 159, 159, 0.9);
}

